.video-item {
    background: #f6f5f0;
    padding: 16px;
    cursor: pointer;
    border-radius: 0.5em;
    transition: all 0.2s;
}

.video-item + .video-item {
    margin-top: 16px;
}

.video-item img {
    width: 100%;
}

.video-item .video-title {
    font-size: 11px;
    font-weight: 600;
    transition: color 0.2s;
}

.video-item:hover > .video-title {
    color: #c39e9e;
}

.video-item .channel-title {
    color: #2d2d2b;
    font-size: 9px;
    margin-top: 5px;
}