body {
  background: #f0e4d8;
  width: 100vw;
  min-height: 100vh;
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5rem 0;
}

.space-y-4 > * + * {
  margin-top: 2em;
}

h1 {
  font-size: 2em;
  font-weight: 600;
  color: #2d2d2b;
  text-align: center;
  text-transform: uppercase;
}