.video-details {
    background: #f6f5f0;
    padding: 16px;
    border-radius: 0.5em;
    margin-bottom: 1.5rem;
    transition: all 0.2s;
}

.video-title {
    font-size: 1.5em;
    font-weight: 600;
}

.video-description {
    margin-top: 10px;
}

.channel-title {
    font-size: 12px;
    margin-top: 5px;
    color: #2d2d2b;
}

.video-wrapper {
    width: 100%;
    height: 0;
    padding-bottom: calc(9/16 * 100%);
    position: relative;
    margin-bottom: 3rem;
}

.video-wrapper iframe {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border: 0;
}